/* roboto-500 - latin_cyrillic */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""),
    url("../fonts/roboto-v27-latin_cyrillic-500.woff2") format("woff2"), /* Chrome , Opera 23+, Firefox 39+ */
    url("../fonts/roboto-v27-latin_cyrillic-500.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
